*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  height: 100%;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

a {
  text-decoration: none;
}

@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-display: swap;
}
.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 11px 15px;
  font-family: "Oswald", "Verdana", sans-serif;
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  color: #ffffff;
  text-transform: uppercase;
  background-color: #68b738;
  border: none;
  transition: all 0.2s ease-out;
  cursor: pointer;
}
.button:hover {
  background-color: #5eaa2f;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
.button--full-width {
  width: 100%;
}
.button--secondary {
  background-color: #f2f2f2;
  color: #444444;
}
.button--secondary:hover {
  background-color: #ebebeb;
}
.button--secondary:active {
  color: rgba(68, 68, 68, 0.3);
}

.title {
  margin: 0;
  font-family: "Oswald", "Verdana", sans-serif;
  font-weight: 400;
  color: #000000;
}

.container {
  margin: 0 auto;
  padding: 0 20px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 30px;
  }
}
@media (min-width: 1280px) {
  .container {
    max-width: 1280px;
  }
}
@media (min-width: 768px) {
  .container--form {
    padding: 0 69px;
  }
}
@media (min-width: 1280px) {
  .container--form {
    padding: 0 30px;
  }
}

@media (min-width: 1280px) {
  .index-page::before {
    content: "";
    position: absolute;
    top: 0;
    left: 50%;
    background-image: url("/img/cat-bg-desktop.jpg"), linear-gradient(#64a73c, #64a73c);
    background-repeat: no-repeat;
    background-position: right center, center;
    width: 50%;
    height: 694px;
    max-width: 720px;
  }
}
@media (min-width: 1280px) and (min-resolution: 2dppx) {
  .index-page::before {
    background-image: url("/img/cat-bg-desktop@2x.jpg"), linear-gradient(#64a73c, #64a73c);
    background-size: cover;
    width: 50%;
  }
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.case {
  background-color: #eaeaea;
  padding-top: 25px;
  padding-bottom: 44px;
}
@media (min-width: 768px) {
  .case {
    padding-top: 19px;
    padding-bottom: 60px;
    background: linear-gradient(to top, #eaeaea 0 462px, transparent 462px 100%);
  }
}
@media (min-width: 1280px) {
  .case {
    background: linear-gradient(to top, #f2f2f2 0 460px, transparent 460px 100%);
    padding-top: 0;
    padding-bottom: 15px;
    margin: 0;
  }
}

@media (min-width: 768px) {
  .case__wrapper {
    margin-bottom: 60px;
  }
}

.case__title {
  margin: 0;
  margin-bottom: 44px;
  font-size: 36px;
  line-height: 36px;
}
@media (min-width: 768px) {
  .case__title {
    margin-bottom: 65px;
    font-size: 60px;
    line-height: 60px;
  }
}
@media (min-width: 1280px) {
  .case__title {
    padding-top: 45px;
    margin-bottom: 146px;
  }
}

.case__text {
  display: block;
  margin-bottom: 20px;
  font-size: 13px;
  letter-spacing: 0.5px;
}
@media (min-width: 768px) {
  .case__text {
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 50px;
    letter-spacing: 0;
    padding-right: 10px;
  }
}
@media (min-width: 1280px) {
  .case__text {
    margin-bottom: 69px;
    min-width: 435px;
    padding-right: 0;
  }
}

@media (min-width: 768px) {
  .case__details-wrapper {
    display: flex;
    justify-content: space-between;
  }
}
@media (min-width: 1280px) {
  .case__details-wrapper {
    display: block;
    width: 335px;
  }
}

.case__summary {
  display: flex;
  justify-content: center;
  margin-bottom: 40px;
  line-height: 20px;
  text-transform: uppercase;
  text-align: center;
}
@media (min-width: 768px) {
  .case__summary {
    display: block;
    margin-top: 13px;
    margin-right: 4px;
    margin-bottom: 0;
    text-align: end;
  }
}
@media (min-width: 1280px) {
  .case__summary {
    display: flex;
    justify-content: space-between;
    margin-top: 0;
  }
}

.case__summary span {
  display: block;
}
@media (min-width: 768px) {
  .case__summary span {
    font-size: 20px;
    margin-bottom: 10px;
  }
}
@media (min-width: 1280px) {
  .case__summary span {
    margin: 0;
  }
}

@media (min-width: 1280px) {
  .case__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 140px;
  }
}

.catalog-extra {
  padding-top: 25px 0 35px;
}
@media (min-width: 768px) {
  .catalog-extra {
    padding-top: 68px;
    padding-bottom: 76px;
  }
}
@media (min-width: 1280px) {
  .catalog-extra {
    padding-top: 76px;
    padding-bottom: 83px;
  }
}

.catalog-extra__title {
  margin-bottom: 35px;
  font-size: 24px;
  line-height: 24px;
}
@media (min-width: 768px) {
  .catalog-extra__title {
    margin-bottom: 80px;
    padding-left: 37px;
    font-size: 36px;
    line-height: 36px;
  }
}
@media (min-width: 1280px) {
  .catalog-extra__title {
    margin-bottom: 77px;
    padding-left: 0;
  }
}

@media (min-width: 1280px) {
  .catalog-extra__wrapper {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 80px;
  }
}

.catalog-extra__list {
  margin-bottom: 35px;
}
@media (min-width: 768px) {
  .catalog-extra__list {
    margin-bottom: 69px;
  }
}
@media (min-width: 1280px) {
  .catalog-extra__list {
    grid-column: 1/4;
    margin-bottom: 0;
  }
}

.catalog-page {
  padding-top: 29px;
}
@media (min-width: 768px) {
  .catalog-page {
    padding-top: 0;
  }
}

.catalog-page__title {
  margin-bottom: 41px;
  font-size: 36px;
  line-height: 36px;
}
@media (min-width: 768px) {
  .catalog-page__title {
    margin-bottom: 82px;
    font-size: 60px;
    line-height: 60px;
  }
}

.catalog-page__container {
  margin-bottom: 26px;
}
@media (min-width: 768px) {
  .catalog-page__container {
    margin-bottom: 0;
  }
}

.checkbox__label {
  position: relative;
  display: block;
  padding: 6px 0 8px 45px;
  font-size: 16px;
  text-transform: uppercase;
  cursor: pointer;
}
@media (min-width: 768px) {
  .checkbox__label {
    font-size: 20px;
    line-height: 22px;
    padding-top: 11px;
    padding-left: 59px;
  }
}
.checkbox__label::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 30px;
  height: 30px;
  border: 1px solid #d9d9d9;
}
@media (min-width: 768px) {
  .checkbox__label::before {
    width: 40px;
    height: 40px;
  }
}

.checkbox__control:focus-visible + .checkbox__label {
  outline: 2px solid #444444;
  outline-offset: 2px;
}
.checkbox__control:hover + .checkbox__label::before {
  border: 1px solid #bababa;
}
.checkbox__control:checked + .checkbox__label::before {
  background-image: url("/img/checkbox-checked.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 20px 20px;
}
@media (min-width: 768px) {
  .checkbox__control:checked + .checkbox__label::before {
    background-size: 26px 26px;
  }
}
.checkbox__control:disabled + .checkbox__label {
  opacity: 0.3;
  pointer-events: none;
}

.contacts {
  position: relative;
}
.contacts--colored {
  background-color: #f2f2f2;
}
.contacts iframe {
  border: none;
  display: block;
  width: 100%;
  height: 100%;
}

.contacts__map-container {
  height: 362px;
  background-image: url("/img/location.jpg");
  background-repeat: no-repeat;
  background-position: center;
}
@media (min-resolution: 2dppx) {
  .contacts__map-container {
    background-image: url("/img/location@2x.jpg");
    background-size: 320px, 362px;
  }
}
@media (min-width: 768px) {
  .contacts__map-container {
    height: 400px;
    background-image: url("/img/location-tablet.jpg");
    background-size: 768px, 400px;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .contacts__map-container {
    background-image: url("/img/location-tablet@2x.jpg");
    background-size: 768px, 400px;
  }
}
@media (min-width: 1280px) {
  .contacts__map-container {
    background-image: url("/img/location-desktop.jpg");
    background-size: 1440px, 400px;
  }
}
@media (min-width: 1280px) and (min-resolution: 2dppx) {
  .contacts__map-container {
    background-image: url("/img/location-desktop@2x.jpg");
    background-size: 1440px, 400px;
  }
}

.contacts__map-image {
  display: block;
  margin: 0 auto;
  height: 362px;
  width: auto;
}
@media (min-width: 768px) {
  .contacts__map-image {
    height: 400px;
  }
}
.contacts__map-image--hidden {
  display: none;
}

.contacts__container {
  display: flex;
  justify-content: space-between;
  padding: 26px 0;
}
@media (min-width: 768px) {
  .contacts__container {
    padding: 60px 40px 60px;
  }
}
@media (min-width: 1280px) {
  .contacts__container {
    position: absolute;
    top: 98px;
    width: 565px;
    padding: 56px 80px;
    background-color: #ffffff;
  }
}
@media (min-width: 768px) {
  .contacts__container--form {
    padding: 56px 0;
  }
}
@media (min-width: 1280px) {
  .contacts__container--form {
    padding: 56px 80px;
    background-color: #ffffff;
  }
}

.contacts__title {
  max-width: 140px;
  margin: 0;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .contacts__title {
    font-size: 20px;
    line-height: 26px;
  }
}

.contacts__location {
  max-width: 170px;
}
@media (min-width: 768px) {
  .contacts__location {
    font-size: 16px;
    line-height: 20px;
  }
}

.contacts__address {
  display: block;
  margin-bottom: 3px;
}
@media (min-width: 768px) {
  .contacts__address {
    margin-top: -4px;
    margin-bottom: 20px;
    text-align: right;
  }
}
@media (min-width: 1280px) {
  .contacts__address {
    text-align: left;
  }
}

@media (min-width: 768px) {
  .contacts__city {
    float: right;
  }
}
@media (min-width: 1280px) {
  .contacts__city {
    text-align: left;
    float: left;
  }
