.copyright {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.copyright__text {
  font-size: 16px;
  line-height: 18px;
  margin-top: 6px;
}
@media (min-width: 768px) {
  .copyright__text {
    display: block;
    margin-right: 22px;
    margin-top: 13px;
  }
}

.copyright__link {
  display: flex;
  color: #666666;
  transition: all 0.2s ease-out;
}
@media (max-width: 768px) {
  .copyright__link {
    width: 100%;
    justify-content: space-between;
  }
}
.copyright__link:hover, .copyright__link:focus-visible {
  color: #68b738;
}
.copyright__link:disabled {
  opacity: 0.3;
}

.details {
  padding-top: 24px;
  padding-bottom: 49px;
}
@media (min-width: 768px) {
  .details {
    padding-top: 47px;
    padding-bottom: 63px;
    border-bottom: 1px solid #e6e6e6;
  }
}
@media (min-width: 1280px) {
  .details {
    padding-top: 74px;
    margin-bottom: 15px;
    padding-bottom: 0;
    border-bottom: none;
  }
}

.details__list {
  counter-reset: details;
}
@media (min-width: 768px) {
  .details__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 59px;
    row-gap: 71px;
  }
}
@media (min-width: 1280px) {
  .details__list {
    grid-template-columns: repeat(4, 1fr);
    column-gap: 79px;
  }
}

.details__item {
  position: relative;
  display: flex;
  margin-bottom: 20px;
  align-items: center;
  counter-increment: details;
}
.details__item:last-child {
  margin-bottom: 0;
}
.details__item::before {
  content: "";
  display: block;
  width: 60px;
  height: 60px;
  background-color: #68b738;
  background-position: center;
  background-repeat: no-repeat;
  flex-shrink: 0;
}
