.details {
  padding-top: 24px;
  padding-bottom: 49px;

  @media (min-width: $tablet-width) {
    padding-top: 47px;
    padding-bottom: 63px;
    border-bottom: 1px solid $style-gray-300;
  }

  @media (min-width: $desktop-width) {
    padding-top: 74px;
    margin-bottom: 15px;
    padding-bottom: 0;
    border-bottom: none;
  }
}

.details__list {
  counter-reset: details;

  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 59px;
    row-gap: 71px;
  }

  @media (min-width: $desktop-width) {
    grid-template-columns: repeat(4, 1fr);
    column-gap: 79px;
  }
}

.details__item {
  position: relative;
  display: flex;
  margin-bottom: 20px;
  align-items: center;
  counter-increment: details;

  &:last-child {
    margin-bottom: 0;
  }

  &::before {
    content: "";
    display: block;
    width: 60px;
    height: 60px;
    background-color: $basic-green;
    background-position: center;
    background-repeat: no-repeat;
    flex-shrink: 0;
  }

