  @media (min-width: $tablet-width) {
    margin-bottom: 63px;
    padding-right: 61px;
    flex-direction: column;
    align-items: flex-start;

    &:last-child {
      margin-bottom: 63px;
    }

    &::before {
      width: 80px;
      height: 80px;
    }


    &::after {
      content: counter(details);
      position: absolute;
      font-family: $custom-font;
      font-size: 280px;
      line-height: 1;
      color: $basic-gray;
      z-index: -1;
      top: -37px;
      left: 117px;
    }
  }

  @media (min-width: $desktop-width) {
    padding-right: 0;
  }
}

.details__title {
  margin: 0;
  margin-bottom: 42px;
  font-size: 36px;
  line-height: 36px;

  @media (min-width: $tablet-width) {
    font-size: 60px;
    line-height: 60px;
    margin-bottom: 72px;
  }
}

.details__text {
  margin-left: 20px;

  @media (min-width: $tablet-width) {
    margin-left: 0;
    margin-top: 31px;
    line-height: 24px;
  }
}
