.container {
  margin: 0 auto;
  padding: 0 20px;

  @media (min-width: $tablet-width) {
    padding: 0 30px;
  }

  @media (min-width: $desktop-width) {
    max-width: $desktop-width;
  }

  &--form {
    @media (min-width: $tablet-width) {
      padding: 0 69px;
    }

    @media (min-width: $desktop-width) {
      padding: 0 30px;
    }
  }
}
